@import '~node_modules/modern-normalize/modern-normalize.css';

:root {
  /* rgb(119. 122. 131) */
  --text-color: #777a83;

  --text-color-light: rgb(173, 175, 181);

  /* rgb(58. 61. 73) */
  --headers-color: #3a3d49;

  --background-color: #ffffff;
  --buttons-background-color: #ffd141;
  --buttons-text-color: #3a3d49;

  --base-transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: calc(21 / 14);
  color: var(--text-color);
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
p,
button {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
}

address {
  font-style: normal;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

